h2 {
  color: #eaded0;
  font-size: 24px;
  font-weight: bold;
  margin: 0;
}

h3 {
  display: inline;
  color: #eaded0;
  font-size: 14px;
  font-weight: bold;
}

hr.spacer {
  border: 0px;
  margin: 0;
}

h1 {
  text-align: center;
  color: #eaded0;
  font-size: 55px;
  font-weight: bold;
  margin: 0;
}

hr {
  border-width: 3px 0 0 0;
  border-style: solid;
  border-color: #6699cc;
  margin-bottom: 0px;
  margin-top: 20px;
}

body {
  font-family: "Fira Mono", monospace;
  background-color: #0b121c;
  color: #ccccff;
  text-align: left;
  width: 90%;
  margin: auto;
  display: block;
  font-size: 14px;
  white-space: pre-wrap;
  word-wrap: normal;
}

span.bang {
  color: #9966cc;
}

span.quest {
  color: #33cc66;
}

span {
  display: inline-block;
}

a {
  color: #6699cc;
  text-decoration: none;
  font-size: 16px;
}
